<template>
  <div>
    <RightTitle title=""></RightTitle>

    <!--  -->
    <div class="uar_form">
      <el-form ref="form" :inline="true" :model="filter" label-width="80px" size="medium">
        <el-form-item label="">
          <el-input v-model="filter.title" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="addTemplate"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <div class="uar_form">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="" prop="type" width="100" align="center"></el-table-column>
        <el-table-column label="" prop="time" width="150" align="center"></el-table-column>
        <el-table-column label="" prop="status" width="120" align="center"></el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <el-button-group>
              <el-tooltip content="">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  size="small"
                  @click="showInfo(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip content="">
                <el-button type="primary" icon="el-icon-edit" size="small"></el-button>
              </el-tooltip>
              <el-tooltip content="">
                <el-button
                  type="primary"
                  icon="el-icon-delete"
                  size="small"
                  @click="deleteArt(scope.row)"
                ></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="uar_form page_area">
      <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
    </div>

    <!--  -->
    <el-dialog :title="dialogTitle" :visible.sync="formVisible" width="40%">
      <EffectsForm></EffectsForm>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="dialogTitle" :visible.sync="infoVisible" width="40%">
      <EffectsInfo :artId="infoArtId"></EffectsInfo>
    </el-dialog>
  </div>
</template>

<script>
import RightTitle from "../../../components/RightTitle";
import EffectsForm from "./components/EffectsForm";
import EffectsInfo from "./components/EffectsInfo";
export default {
  name: "course_effects",
  components: {
    RightTitle,
    EffectsForm,
    EffectsInfo
  },
  data() {
    return {
      filter: {
        title: ""
      },
      tableData: [
        {
          id: 1,
          title: "APP",
          type: "",
          time: "2018-08-21 15:58",
          status: ""
        },
        {
          id: 2,
          title: "APP",
          type: "",
          time: "2018-08-21 15:58",
          status: ""
        }
      ],
      formVisible: false,
      infoVisible: false,
      dialogTitle: "",
      infoArtId: 0
    };
  },
  methods: {
    //   
    addTemplate() {
      this.formVisible = true;
      this.dialogTitle = "";
    },
    showInfo(row) {
      this.infoVisible = true;
      this.dialogTitle = "";
      this.infoArtId = row.id;
    },
    deleteArt(row) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          console.log(row);
        })
        .catch(() => {});
    }
  }
};
</script>

