<template>
  <div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="first">
          <div class="scroll_content">
            <el-form-item label="" style="width:500px;">
              <el-input v-model="form.title" clearable placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" style="width:500px;">
              <el-select v-model="form.industry" clearable>
                <el-option label="" value="8"></el-option>
                <el-option label="" value="9"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.type" clearable>
                <el-option label="jquery" value="8"></el-option>
                <el-option label="Html5" value="9"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="form.price" controls-position="right" :min="0"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.isOriginal">
                <el-radio label="0"></el-radio>
                <el-radio label="1"></el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <vue-ueditor-wrap
            ref="ueditor"
            v-model="form.content"
            :destroy="false"
            :config="editorConfig"
            @ready="ready"
          ></vue-ueditor-wrap>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <div class="scroll_content">
            <el-input
              type="textarea"
              placeholder=""
              resize="none"
              v-model="form.code"
              :rows="20"
            ></el-input>
            <div class="ClearsH1"></div>
            <el-button type="primary" round @click="prevEffects"></el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="forth">
          <div class="scroll_content">
            <el-scrollbar class="page-component__scroll">
              <el-upload
                class="upload-demo"
                ref="upload"
                action="https://jsonplaceholder.typicode.com/posts/"
                :file-list="form.fileList"
                :auto-upload="false"
                list-type="picture"
                :limit="1"
                :multiple="false"
              >
                <el-button slot="trigger" size="small" type="primary"></el-button>
                <el-button style="margin-left: 10px;" size="small" type="success"></el-button>
                <div slot="tip" class="el-upload__tip">jpg,jpeg,png,gif100KB</div>
              </el-upload>
            </el-scrollbar>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="fifth">
          <div class="download_content">
            <el-scrollbar class="page-component__scroll">
              <!--  -->
              <el-upload
                  class="upload-demo"
                  ref="upload"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :file-list="form.localFile"
                  :auto-upload="false"
                  :limit="1"
                  :multiple="false"
                >
                  <el-button slot="trigger" size="small" type="primary"></el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success"></el-button>
                  <div slot="tip" class="el-upload__tip">zip2MB</div>
                </el-upload>
            </el-scrollbar>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </div>
</template>

<script>
import VueUeditorWrap from "vue-ueditor-wrap"; // ES6 Module
export default {
  name: "template_form",
  components: {
    VueUeditorWrap
  },
  props: {
    artId: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      activeName: "first",
      form: {
        title: "",
        industry: "",
        type: "",
        language: "",
        format: [],
        color: "",
        price: 0,
        isOriginal: "0",
        content: "",
        fileList: [],
        download_type: [],
        localFile: [],
        download2_url: "",
        download2_pwd: ""
      },
      editorConfig: {
        // 
        autoHeightEnabled: false,
        // 
        initialFrameHeight: 440,
        // 
        initialFrameWidth: "500px",
        // 
        // serverUrl: "http://admin.azz.lc/Public/plugin/ueditor/php/controller.php",
        // UEditor  vue-cli vue-ueditor-wrap 2
        // UEDITOR_HOME_URL: "/static/UEditor/",
        // Request Headers,https://github.com/HaoChuan9421/ueditor/commits/dev-1.4.3.3
        // headers: {
        //   access_token: '37e7c9e3fda54cca94b8c88a4b5ddbf3'
        // },
        toolbars: [
          [
            "Undo",
            "Redo",
            "Bold",
            "italic",
            "underline",
            "forecolor",
            "backcolor",
            "insertorderedlist",
            "insertunorderedlist",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyjustify",
            "emotion",
            "simpleupload",
            "insertcode",
            "autotypeset",
            "inserttable",
            "spechars",
            "link",
            "unlink"
          ]
        ]
      }
    };
  },
  methods: {
    ready(editorInstance) {
      console.log(`${editorInstance.key}:`, editorInstance);
      console.log(111);
    },
    // 
    prevEffects() {
      let codeHtml = this.form.code.trim();
      if (codeHtml == "") {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        let pop = window.open("", "_blank", "");
        pop.document.open("text/html", "replace");
        pop.opener = null; // 
        pop.document.write(codeHtml);
        pop.document.close();
      }
    }
  },
  watch: {
    artId(newValue, oldValue) {
      // 
      console.log('' + newValue);
      console.log('' + oldValue);
    }
  }
};
</script>

<style scoped>
.download_content {
  height: 400px;
  overflow: hidden;
}
</style>